body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --large-regular: Ubuntu;

  /* font sizes */
  --large-regular-size: 18px;
  --small-bold-size: 14px;
  --base-regular-size: 16px;
  --lead-bold-size: 20px;
  --h3-size: 28px;
  --font-size-3xl: 22px;
  --extrasmall-regular-size: 12px;
  --h4-size: 24px;

  /* Colors */
  --color-white-100: #ffffff;
  --white: #fff;
  --gray-700: #787a80;
  --gray-900: #1e212c;
  --color-gray-100: rgba(255, 255, 255, 0.6);
  --gray-800: #424551;
  --primary: #ff5a30;
  --gray-300: #e5e8ed;
  --gray-400: #d6dadd;
  --divider-gradient: rgba(218, 219, 221, 0);
  --color-orangered-100: #e64017;
  --gray-200: #f5f5f7;
  --gray-600: #9a9da5;

  /* Gaps */
  --gap-xl: 20px;
  --gap-17xl: 36px;
  --gap-xs: 12px;
  --gap-41xl: 60px;
  --gap-5xl: 24px;
  --gap-5xs: 8px;
  --gap-11xl: 30px;
  --gap-21xl: 40px;
  --gap-xs-5: 11.5px;
  --gap-base: 16px;
  --gap-smi: 13px;
  --gap-9xs: 4px;

  /* Paddings */
  --padding-11xs: 2px;
  --padding-xl: 20px;
  --padding-2xl: 21px;
  --padding-11xl: 30px;
  --padding-20xl: 39px;
  --padding-12xl: 31px;
  --padding-6xs: 7px;
  --padding-10xs-5: 2.5px;
  --padding-8xs-5: 4.5px;
  --padding-13xl: 32px;
  --padding-61xl: 80px;
  --padding-326xl: 345px;
  --padding-67xl: 86px;
  --padding-153xl: 172px;
  --padding-12xs: 1px;
  --padding-4xs: 9px;
  --padding-base: 16px;
  --padding-3xs: 10px;

  /* Border radiuses */
  --br-9xs: 4px;

  /* Effects */
  --shadow-small: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
    0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04),
    0px 4px 4px -4px rgba(30, 33, 44, 0.03);
  --shadow-large: 0px 60px 80px -20px rgba(30, 33, 44, 0.16),
    0px 26px 24px -10px rgba(30, 33, 44, 0.1),
    0px 12px 10px -6px rgba(30, 33, 44, 0.08),
    0px 4px 4px -4px rgba(30, 33, 44, 0.05);
}
